    /*padding-top: 15px;*/
}

.bg-blur{
  /* Absolutely position it, but stretch it to all four corners, then put it just behind .bg-opacity's z-index */
  position: absolute;
  top: 0px;
  right: 0px;
  bottom: 0px;
  left: 0px;
  z-index: 99;

  /* Pull the background 70px higher to the same place as #bg's */
  background-position: center;
  -webkit-filter: blur(6px);
  background: url(/react-video-live-demo/static/media/app-background-small.a47393c1.jpg) no-repeat 50% fixed;
  filter: blur(6px);
  background-size: cover;
  margin:6px;
}

